}
.boxshadowtogle{box-shadow: 0 0 0 0vw #46bc98, 0 0 0 0vh #46bc98;}

.youtubemob, .facebookmob, .twittermob{display: none !important}

.dropdown-menu {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1000;
    display: none;
    float: left;
    min-width: 160px;
    padding: 5px 0;
    margin: 2px 0 0;
    list-style: none;
    font-size: 14px;
    text-align: left;
    background-color: #fff;
    border: 1px solid #ccc;
    border: 1px solid rgba(0,0,0,.15);
    border-radius: 4px;
    box-shadow: 0 6px 12px rgba(0,0,0,.175);
    background-clip: padding-box;
}

header{background: #46bc98;}
.navbar-light .navbar-toggler{border:0}
.navbar-light .navbar-toggle a:active,.navbar-light .navbar-toggle a:focus,.navbar-light .navbar-toggle a:visited{outline:0;border:none}
.animated-icon1{width:30px;height:20px;position:relative;margin:0;-webkit-transform:rotate(0deg);-moz-transform:rotate(0deg);-o-transform:rotate(0deg);transform:rotate(0deg);-webkit-transition:.5s ease-in-out;-moz-transition:.5s ease-in-out;-o-transition:.5s ease-in-out;transition:.5s ease-in-out;cursor:pointer}
.animated-icon1 span{display:block;position:absolute;height:3px;width:100%;border-radius:9px;opacity:1;left:0;-webkit-transform:rotate(0deg);-moz-transform:rotate(0deg);-o-transform:rotate(0deg);transform:rotate(0deg);-webkit-transition:.25s ease-in-out;-moz-transition:.25s ease-in-out;-o-transition:.25s ease-in-out;transition:.25s ease-in-out;background:#000}
.animated-icon2 span{background:#000}
.animated-icon3 span{background:#000}
.animated-icon1 span:nth-child(1){top:0}
.animated-icon1 span:nth-child(2){top:10px}
.animated-icon1 span:nth-child(3){top:20px}
.animated-icon1.open span:nth-child(1){top:11px;-webkit-transform:rotate(135deg);-moz-transform:rotate(135deg);-o-transform:rotate(135deg);transform:rotate(135deg)}
.animated-icon1.open span:nth-child(2){opacity:0;left:-60px;display:none}
.animated-icon1.open span:nth-child(3){top:11px;transform:rotate(-135deg)}
.navbar-toggler:not(:disabled):not(.disabled){outline:none}
.amber.lighten-4{background-color:#46bc98!important;padding:0 1rem; margin-bottom: 10px;z-index: 10000; max-width: 1400px}
.navbar.navbar-light .breadcrumb .nav-item.active>.nav-link,.navbar.navbar-light .navbar-nav .nav-item.active>.nav-link{background-color:rgba(70,188,152)}
.socialmedia a{display:inline-block;margin-right:5px;background:#42a98a;border-radius:50%;width:30px;height:30px;color:#fff;padding:3px;text-align:center}
.socialmedia a i{font-size:15px}
.prgnancybox{position:relative;overflow:hidden}
.prgnancybox:before{content:'';background:repeating-linear-gradient(135deg,rgba(255,255,255,.5),rgba(255,255,255,.5) 5px,transparent 7px,transparent 15px,rgba(255,255,255,.5) 10px);height:100%;width:100%;opacity:.3;transform:rotate3d(1,1,0,90deg);position:absolute;left:0;top:0;z-index:1;transition:all .3s}
.prgnancybox:hover:before{transform:rotate3d(1,1,0,0deg)}
.prgnancybox img{width:100%;height:auto;transition:all .3s ease}
.prgnancybox:hover img{filter:hue-rotate(40deg) saturate(2)}
.prgnancybox .prgnancybox-content{color:#fff;font-family:'Merriweather',serif;text-shadow:1px 2px #000;text-align:center;width:100%;padding:5px 10px;position:absolute;bottom:24%;left:0;z-index:1;transition:all .35s ease}
.prgnancybox-content a{color:#fff}
.prgnancybox:hover .prgnancybox-content{opacity:1;filter:blur(0)}
.prgnancybox .prgnancytitle{font-size:35px;font-family:'roboto';font-weight:400;text-shadow:1px 2px #000;text-transform:uppercase}
.prgnancybox .prgnancytitle span{font-size:45px;font-weight:700;display:block}
.prgnancybox .prgnancypost{font-size:16px;text-transform:capitalize;border:1px solid #fff;border-radius:5px;padding:4px 8px;margin-top:10px;display:inline-block}
.prgnancybox .prgnancyicon{padding:0;margin:0;list-style:none;opacity:0;transform:rotate(50deg);transform-origin:right bottom;position:absolute;right:10px;top:10px;z-index:2;transition:all .3s}
.prgnancybox:hover .prgnancyicon{opacity:1;transform:rotate(0)}
.prgnancybox .prgnancyicon li a{color:#fff;background:#ff4757;font-size:20px;text-align:center;line-height:40px;height:40px;width:40px;margin-bottom:10px;border-radius:50%;display:block;position:relative;transition:all .3s}
.prgnancybox .prgnancyicon li a:hover{color:#ff4757;background-color:#fff;box-shadow:-2px 2px 0 2px #ff4757}

