/*navigation*/

.navigationtop .menu{position:absolute;left:-100px;top:-90px;z-index:100;width:180px;height:175px;background:#46bc98;border-radius:50% 50% 50% 50%;-webkit-transition:.5s ease-in-out;transition:.5s ease-in-out;box-shadow:0 0 0 0 #FFF,0 0 0 0 #FFF;cursor:pointer}

.navigationtop .hamburger{position:absolute;top:135px;left:115px;width:30px;height:2px;background:#000;display:block;-webkit-transform-origin:center;transform-origin:center;-webkit-transition:.5s ease-in-out;transition:.5s ease-in-out}

.navigationtop .hamburger:after,label .hamburger:before{-webkit-transition:.5s ease-in-out;transition:.5s ease-in-out;content:"";position:absolute;display:block;width:100%;height:100%;background:#000}

.navigationtop .hamburger:before{top:-10px}

.navigationtop .hamburger:after{bottom:-10px}
.navigationtop input{display:none}
.navigationtop input:checked + .menu{box-shadow:0 0 0 100vw #46bc98,0 0 0 100vh #46bc98;border-radius:0}
.navigationtop input:checked + .menu .hamburger{-webkit-transform:rotate(45deg);transform:rotate(45deg)}
.navigationtop input:checked + .menu .hamburger:after{-webkit-transform:rotate(90deg);transform:rotate(90deg);bottom:0}
.navigationtop input:checked + .menu .hamburger:before{-webkit-transform:rotate(90deg);transform:rotate(90deg);top:0}
.navigationtop input:checked + .menu + ul{opacity:1}

.navigationtop ul{z-index:200;position:absolute;opacity:0;-webkit-transition:.25s 0 ease-in-out;transition:.25s 0 ease-in-out; margin-top: 80px; width: 100%; display: none; flex-direction: row;display: flex;align-items: center; left: -100%;transition: all .5s ease-in;}

.navigationtopshow{left: 20px !important}

.navigationtop ul li{display: inline-block;transition:.25s 0 ease-in-out; flex: 1 1 auto;}

.navigationtop a{display:block;color:#000;text-decoration:none;}
.disblock{display: block !important; transition: all 2s ease-in;}
/*end of navigation*/

